*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --background:  hsl(180, 100%, 5%);
    --textcolor: white;
    --search-card: hsla(179, 100%, 11%, 0.388);
    --btn: hsl(156, 100%, 47%);
    --lightcard:hsl(172, 100%, 11%);
}
.light{
    --background:  hsl(50, 21%, 95%);
    --textcolor: rgb(0, 0, 0);
    --search-card: hsla(0, 0%, 69%, 0.429);
    --btn: hsl(245, 100%, 66%);
    --lightcard:hsla(150, 1%, 55%, 0.34);
}

body{
    background-color: var(--background);
    color: var(--textcolor);
    font-family: "Poppins", sans-serif;
}
#root{
    min-height: 100vh;
    display: flex;
}
.card{
    margin: auto;
    min-width: 600px;
    border-radius: 15px;
    padding: 2rem;
}
.toggle{
    padding: 0 1rem;
    outline: none;
    border: none;
    background-color: transparent;
    color: var(--textcolor);
    cursor: pointer;
}
.btn{
    padding: 0 1rem;
    outline: none;
    border: none;
    cursor: pointer;
    background-color: var(--btn);
    border-radius: 8px;
    color: var(--background);

}
.navbar{
    display: flex;
    justify-content: space-between;
}
.search{
    width: 100%;
    display: flex;
    margin-top:2rem;
    background-color: var(--search-card);
    padding: 8px;
    border-radius: 8px;
}
.search input{
    width: 100%;
    font-size: 1rem;
    padding: 5px;
    outline: none;
    border: none;
    border: 2px solid transparent;
    background-color: transparent;
    color: var(--textcolor);

    
    
}
.api-limit{
    margin-top: 1rem;
    color: rgb(255, 221, 0);
    text-align: center;
}

/* card  */

.image{
    height: 100px;
    width: 100px;
    border: 4px solid rgb(0, 101, 35);
    border-radius: 50%;
    margin-right: 2rem;

}
.image img{
    width: 100%;
    object-fit: contain;
    object-position: center;
    border-radius: 50%;
}
.carddatabox{
    display: flex;
    margin-top: 2rem;
    background-color: var(--search-card);
    padding: 1rem 10px;
    border-radius: 8px;

    
}
.cardbox{
    width: 100%;
}
.username{
    display: flex;
    justify-content: space-between;
    margin-bottom: 1.2rem;

}
.username p{
    font-size: 1rem;
    font-weight: 400;
    margin-top: 8px;
}
.card-data-box{
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    background-color: var(--lightcard);
    margin-top: 1.5rem;
    border-radius: 10px;
}
.icon{
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
}
.card-data{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;;
}
.card-data h3{
    margin-top: 10px;
    font-size: 1.5rem;
}
.icon-data{
    
    padding: 1rem;

    
}
.err{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.3rem;
    margin-top: 2rem;
}

.icon-data div{
    display: flex;
    justify-content: space-between;
}
a{
    margin: 5px;
    color: var(--textcolor);
    font-size: 1.3rem;
    padding: 5px;  
}
a:hover{
    color: var(--btn);
}

@media screen and (max-width:480px) {
    .carddatabox{
        flex-direction: column;
        justify-content: center;
        align-items: center;;
    }
    .image{
        margin: 1rem 0;
    }
    .username{
        flex-direction: column;
    }
    .card{
        padding: 2px;
        min-width: 370px;
    }
    
}